import React, {Component} from "react";
import styled from "styled-components";
import {Link} from "react-router-dom";
import {ProductConsumer} from "../context.js";
import PropTypes from 'prop-types';

 
class Product extends Component {
	render() {
		const {id, title, img, price, inCart} = this.props.product;
		return (
		  <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3">
			<div className="card">
			 <ProductConsumer>
			 {(value)=> (
			 	  <div className="img-container p-5" 
			 	  		onClick={()=>
			 	  		value.handleDetail(id)
			 	  	    }
			 	  >
			        <Link to="/details">
			         <img src={img} alt="product" className="card-img-top px-10" 
			          />
			        </Link>
			        <button className="cart-btn text-capitalize" disabled={inCart?true:false} 
			             onClick={()=> {
			        	 value.addToCart(id);
			        	 value.openModal(id);
			             }}
			        >
			        {inCart?(<p className="text-capitalize mb-0" disabled>in cart</p>):(<i className="fas fa-cart-plus" />)}
			        </button>
			      </div>
			 )}
			 </ProductConsumer>
			{/*card footer*/}
			  <div className="card-footer d-flex justify-content-between">
			    <p className="align-self-center mb-0">
			    {title}
			    </p>
			  <h5 className="text-blue font-italic mb-0">
			    <span className="mr-1">$</span>
			    {price}	
			  </h5>
			  </div>
			</div>
		  </ProductWrapper>
		);
    }
}
/*nustatomi objektų tipai, kad iškart mestų, klaidą, jeigu data.js įvesta kas nors ne taip*/
Product.propTypes = {
	product: PropTypes.shape({
		id:PropTypes.number,
		img:PropTypes.string,
		title:PropTypes.string,
		price:PropTypes.number,
		inCart:PropTypes.boolean
	}).isRequired
}

const ProductWrapper = styled.div`
.card {
    border-color: transparent;
    transition: all 0.6s linear; 
}
.card-footer {
	background: transparent;
	border-top: transparent;
	transition: all 0.6s linear;
}
&:hover {
	.card {
		border: 0.04rem solid rgba(0,0,0,0.2);
		box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.2);
	}
	.card-footer {
		background: rgba(247,247,247);
	}
	.cart-btn {
		cursor: pointer;
		transform: translate(0%, 0%);
		transition: all 0.6s linear;
	}
}
.img-container {
	position: relative;
	overflow: hidden;
}
.card-img-top {
	transition: all 0.6s linear;
	width: "140px";
	height: "140px";
}
.img-container:hover .card-img-top {
	transform: scale(1.3);
}
.cart-btn {
	position: absolute;
	bottom: 0;
	right: 0;
	padding: 0.2rem 0.4rem;
	background: var(--lightBlue);
	border: none;
	color: var(--mainWhite);
	font-size: 1.4rem;
	border-radius: 0.5rem 0 0 0;
	transform: translate(100%, 100%);
	transition: all 1s linear;
}
.cart-btn:hover {
	color: var(--mainBlue);
}

`;

export default Product;